.notifier {
    width: 100%;
    padding: .5rem 3rem;
    border-radius: .3rem;
}

.notifier h6 {
    font-weight: 500;
    font-size: smaller;
    text-align: center;
}

.notifier.error {
    background-color: rgb(243, 184, 184);

}
.notifier.error h6 {
    color: red;
}

.notifier.success {
    background-color: rgb(184, 243, 187);

}
.notifier.success h6 {
    color: green;
}

.notifier.info {
    background-color: rgb(184, 243, 228);

}
.notifier.info h6 {
    color: var(--primaryColor);
}