.pending_payment_intents{
    width: 100%;
    height: 100%;
    padding: 1rem;
}

.pending_payment_intents_table{
    width: 100%;
    height: 100%;
    background-color: var(--cardColor);
    border-radius: .3rem;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.pending_payment_intents_table_body{
    height: 100%;
    width: 100%;
    overflow: auto;
}



.pending_payment_intents_table_table_row{
    width: 100%;
    display: flex;
    justify-content: space-between;
    grid-gap: .5rem;
    padding: .5rem 1rem;
    background-color:rgb(236, 234, 234);
}

.pending_payment_intents_table_table_row_tuple{
    background-color: var(--cardColor);
    border-top: .1rem solidrgb(236, 234, 234);
}

.pending_payment_intents_table_table_row_column{
    width: 100%;
    text-align: center;
    font-size: small;
    border-right: .1rem solid var(--subtitleColor);
    padding: .1rem 0rem;
    color: var(--textColor);
}

.pending_payment_intents_table_table_row_column_title{
    font-weight: bold;
    color: black;
    font-size: inherit;
}

.pending_payment_intents_table_table_row_column_last{
    border-right: none;
    
}

.delete_action_p_i{
    background-color: #ef3038;
    color: white;
    padding: .2rem 1rem;
    border-radius: .3rem;
    cursor: pointer;
    font-weight: 600;
}

.pending_payment_intents_table_body .pending_payment_intents_table_table_row:nth-child(even){
    /* background-color:rgb(236, 234, 234); */
    color: black;
    color: red;
}