.call_to_subcsription{
    height: 100%;
    width: 40rem;
    background-color: var(--bgColor);
    display: flex;
    overflow: hidden;
    border-radius: .6rem;
    max-height: 70%;
}

.call_to_subcsription_left_lol{
    overflow: auto;
    /* height: 100%; */
}

.call_to_subcsription_header{
    justify-content: space-between;
    grid-gap: 1rem;
    align-items: flex-start;
    border-bottom: .1rem solid var(--dividerColor);
    padding: 1.5rem;
}

.call_to_subcsription_steps{
    overflow: auto;
    height: 100%;
    width: 100%;
    /* background-color: green; */
}

.call_to_subcsription_header div:nth-child(1) p span{
    color: var(--primaryColor);
    font-weight: 600;
}


.call_to_subcsription_steps_indicator{
    grid-gap: .5rem;
}

.call_to_subcsription_buttons{
    justify-content: flex-end;
    width: 100%;
    align-items: center;
    grid-gap: .5rem;
    padding: 1rem 1.5rem 1rem 1.5rem;
    border-top: .1rem solid var(--dividerColor);
}

.isRecommended{
    background-color: var(--primaryColor);
    padding: .2rem .5rem;
    border-radius: .2rem;
    color: white;
    font-weight: 500;
    width:max-content;
    height: fit-content;

}



.steps_holder{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: .6rem;
    padding: 0rem 1.5rem;
}

.steps_holder_b_transfer{
    height: 100%;
}
#call_to_subcsription_steps_indicator_selected{
    background-color: var(--primaryColor);
}

.call_to_subcsription_steps_indicator span{
    padding: .2rem 1rem;
    background-color: var(--dividerColor);
    border-radius: .2rem;
}

.call_to_subcsription_left{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: .6rem;
    overflow: auto;
}

.sbPlan_card{
    grid-gap: .7rem;
    align-items: center;
    justify-content: flex-start;
    padding: 1rem;
    /* border: .1rem solid var(--dividerColor); */
    border-radius: .5rem;
    cursor: pointer;
    background-color: var(--cardColor);
}

.sbPlan_card span:nth-child(1){
    height: 1rem;
    width: 1rem;
    padding: .6rem;
    background-color: var(--dividerColor);
    border-radius: 50%;
}

.sbPlan_card .sbPlan_card_tile h5{
    font-size: medium;
    font-weight: 600;
}

.sbPlan_card .sbPlan_card_tile p{
    font-size: small;
}

.sbPlan_card div{
    width: 100%;
}

.sbPlan_card_tile_price{
    font-weight: bold;
}


.sbPlan_card_selected{
    border: .15rem solid var(--primaryColor);
}

.sbPlan_card_selected span:nth-child(1){
    background-color: var(--primaryColor);
}

.sbPlan_card_selected h5{
    font-size: medium;
    font-weight: 600;
    color: var(--primaryColor);
}

.sbPlan_card_selected .sbPlan_card_tile_price{
    color: var(--primaryColor);
}

.accept_bank_transfer_conditions{
    display: flex;
    width: 100%;
    grid-gap: 1rem;
    background-color: rgb(204, 244, 234);
    padding: .5rem;
    align-items: center;
    border-radius: .2rem;
    cursor: pointer;
}

.accept_bank_transfer_conditions p, .accept_bank_transfer_conditions i{
    color: var(--primaryColor);
}

.refCodeP{
    font-weight: bold;
}