.subscription_history_container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: auto;
}
.main_history_container {
  padding: 1.2rem 1rem 0rem 1rem;
  border-radius: 0.3rem;
}
.card_history_container {
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  grid-gap: .5rem;
  padding-bottom: 1rem;
  align-items: flex-end;
  /* justify-content: flex-end; */
  /* background-color: red; */
}
.card_history_header {
  display: flex;
  justify-content: space-between;
  column-gap: 1rem;
  padding: 0.5rem;
  border-bottom: solid 0.1rem var(--dividerColor);
}
.single_card_history {
  height: fit-content;
  min-height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 0.3rem;
  border-bottom: none;
  background-color: var(--cardColor);
  width: 100%;
}
.card_history_header h5 {
  font-size: 1.4rem;
  color: var(--subtitleColor);
}
.card_details {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0rem 0.6rem;
}
.card_details .single_detail_content {
  display: flex;
  justify-content: space-between;
  border-bottom: 0.1rem solid var(--dividerColor);
}

.single_detail_content{
  padding: .4rem 0rem;
  align-items: center;
  /* border-top: .1rem solid var(--dividerColor); */
  /* background-color: green; */
}
.single_detail_content:last-child {
  border-bottom: none;
}
.single_detail_content p {
  font-size: 0.8rem;
  font-weight: 600;
  color: var(--subtitleColor);
}
.single_detail_content h4 {
  font-size: 0.8rem;
  color: var(--textColor);
}
.state {
  width: fit-content;
  background-color: rgb(184, 243, 187);
  font-size: 0.8rem;
  padding: 0 0.8rem;
  font-weight: bold;
  border-radius: 0.2rem;
}
.expired {
  width: fit-content;
  background-color: rgb(255, 196, 196);
  font-size: 0.8rem;
  padding: 0 0.8rem;
  font-weight: bold;
  border-radius: 0.2rem;
}
.expired #expired {
  color: rgb(177, 0, 0);
}
.out_of_bound {
  background-color: rgb(255, 192, 77);
}
.out_of_bound > #out_of_bound {
  color: rgb(179, 116, 0);
}
.state > p {
  color: green;
}
.unavailable_history {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.unavailable_history > p > i {
  font-style: normal;
  color: var(--primaryColor);
  text-decoration: underline;
  cursor: pointer;
}

.card_history_container_button{
  width: fit-content;
}
