.profile_page {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    padding: .5rem;
    column-gap: .6rem;
    overflow: hidden;
}

.profile_page::-webkit-scrollbar {
    width: 0;
}


.profile_page_left {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: .5rem;
    width: 26rem;
    min-width: 26rem;
    max-width: 26rem;
    height: 100%;
    overflow: auto;
    max-height: 100%;
}

.profile_page_left_profile {
    width: 100%;
    background-color: var(--cardColor);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    border-radius: .4rem;
    row-gap: .5rem;
}

.name_verified {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    column-gap: .5rem;
    padding: 0rem .5rem;
}

.name_verified p {
    color: var(--subtitleColor);
    font-size: smaller;
}

.name_verified i {
    color: var(--primaryColor);
}

.name_verified h2 {
    text-align: center;
}

.profile_page_left img {
    background-color: var(--bgColor);
    width: 8rem;
    height: 8rem;
    min-width: 8rem;
    min-height: 8rem;
    border-radius: 50%;
    border: 2px solid var(--primaryColor);
    border-radius: 50%;
    object-fit: cover;
}

.profile_page_right {
    width: 100%;
    height: 100%;
    background-color: var(--cardColor);
    padding: 1rem;
}

.profile_page_left_loading {
    background-color: var(--cardColor);
    padding: 1rem;
}

.profile_page_left_profile_stat {
    display: flex;
    width: 100%;
    /* justify-content: space-around; */
    background-color: var(--cardColor);
}



.profile_page_left_profile_stat_item {
    padding: .5rem 0rem;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
    border-right: .1rem solid var(--dividerColor);
    column-gap: .5rem;
}

.profile_page_left_profile_stat div:last-child {
    border-right: none;
}

.dis_users {
    width: 100%;
    height: 100%;
    overflow: hidden;
}


@media screen and (max-width : 920px) {
    .profile_page {
        display: flex;
        flex-direction: column;
        grid-gap: .5rem;
    }

    .profile_page_left{
        display: flex;
        flex-direction: column;
        overflow: auto;
        width: 100%;
        width: 100%;
        min-width: 100%;
        max-width: 100%;
    }

    .profile_page .main_displayer{
        display: none;
    }

}