nav {
  grid-area: nav;
  position: relative;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  background-color: var(--cardColor);
  border-bottom: .1rem solid var(--dividerColor);
}


.Logo {
  height: 100%;
  display: flex;
  align-items: center;
}

.Logo>#logo {
  color: var(--primaryColor);
  cursor: pointer;
}

.Menu_container {
  display: flex;
  column-gap: 2rem;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.Menu_container>li {
  display: flex;
  cursor: pointer;
  color: var(--subtitleColor);
}

.Menu_container>li>a {
  justify-content: center;
  align-items: center;
  gap: .3rem;
  transition: all 1s ease-in-out;
}

.Menu_container>li>a>h4 {
  color: gray;
  text-decoration: none;
  font-weight: 600;
  font-size: .9rem;
}

.Menu_container .active>h4,
.Menu_container .active>i {
  color: var(--primaryColor);
  /* font-size: 1.3rem; */
}

.Nav_action {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  column-gap: 1em;
}

.Nav_action i{
  cursor: pointer;
  font-size: 1.4rem;
}

.profil_container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.5rem;
  padding: 0.2rem 0.5rem;
  border: 1px solid var(--dividerColor);
  border-radius: 0.5rem;
  height: 90%;
  cursor: pointer;
}

.profil_container:hover {
  box-shadow: 4px 9px 27px -20px rgba(0, 0, 0, 0.73);
  -webkit-box-shadow: 4px 9px 27px -20px rgba(0, 0, 0, 0.73);
  -moz-box-shadow: 4px 9px 27px -20px rgba(0, 0, 0, 0.73);
}

#active_status {
  position: absolute;
  top: 0.4rem;
  right: 0.5rem;
  width: 0.5rem;
  height: 0.5rem;
  background-color: var(--primaryColor);
  border-radius: 50%;
}

#profil {
  margin-top: 2px;
  font-size: 1.8rem;
  color: var(--subtitleColor);
}

#be_a_publisher {
  font-size: .8rem;
  background-color: var(--primaryColor);
  padding: .4rem;
  border-radius: .3rem;
  text-transform: uppercase;
  font-weight: bold;
  color: var(--cardColor);
  cursor: pointer;
}

.small_menu_container {
  position: absolute;
  /* left: -8.7rem; */
  right: 0;
  top: 3.5em;
  width: 15rem;
  height: fit-content;
  padding: 1rem 1.2rem;
  text-align: left;
  background-color: var(--cardColor);
  border-radius: 1rem;
  z-index: 100;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

hr {
  margin-top: 12px;
}

#small_title {
  margin-left: 1rem;
  color: var(--textColor);
}

#small_title:hover {
  cursor: pointer;
  color: var(--primaryColor);
}

.small_menu_title {
  display: flex;
  justify-content: center;
  align-items: center;
}

.small_menu {
  display: flex;
  margin-top: 0.8rem;
  align-items: center;
  column-gap: 0.7rem;
  cursor: pointer;
}

.small_menu>li {
  list-style: none;
  font-weight: 500;
  color: var(--textColor);
}



@media screen and (max-width : 920px) {
  nav {
    padding: 1rem 2rem;
  }

}

@media screen and (max-width : 425px) {
  nav {
    padding: 1rem;
  }
}

.media_query_tester {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: red;
  display: flex;
  flex-direction: row;
}

.media_query_tester_left,
.media_query_tester_right {
  width: 50%;
  height: 100%;
}

.media_query_tester_left {
  background-color: aquamarine;
}

.media_query_tester_right {
  background-color: orange;
  overflow: auto;
}

@media screen and (max-width : 920px) {

  .media_query_tester {
    display: flex;
    flex-direction: column;
    grid-gap: .5rem;
    overflow: auto;
  }

  .media_query_tester_left,
  .media_query_tester_right {
    width: 100%;
  }
  
  .media_query_tester_left{
    height: 100%;
    min-height: 100%;
  }
  
  .media_query_tester_left {
    background-color: aquamarine;
  }
  
  .media_query_tester_right {
    height: fit-content;
    background-color: orange;
    overflow: unset;
  }

}